<script>// svelte stuff
import { onMount, createEventDispatcher } from 'svelte';
import { fade } from 'svelte/transition';
import { connectToChild } from 'penpal';
import IconButton from './components/WalletSelectorIcons.svelte';
import Logo from './assets/Logo.svelte';
export let wallet = null;
export let inputUrl = 'https://peerpiper.github.io/iframe-wallet-sdk/';
// flex dimensions
export let topOffsetHeight = 0;
export let topOffsetWidth = 0;
export let iframeParentHeight = 0;
export let iframeParentWidth = 0;
let iframeOffsetWidth;
export let show;
export let hide;
const dispatch = createEventDispatcher();
let src;
let placeholder = 'Enter Wallet Url';
let iframe;
let focused;
const data = {
    loading: true // load right away
};
// cache user's preferred wallet URL to their browser
onMount(async () => {
    connect();
});
async function handleIframeLoad() {
    data.loading = false;
    let pending;
    const connection = connectToChild({
        // The iframe to which a connection should be made.
        iframe,
        // Methods the parent is exposing to the child.
        methods: {
            setIframeParentHeight(height) {
                iframeParentHeight = height;
            },
            setIframeParentWidth(width) {
                iframeParentWidth = width;
            },
            show() {
                show();
            },
            hide() {
                hide();
            },
            // walletReady gets called from wallet-sdk when
            // connectionsReady is called which is called when
            // loadedKeys is fired
            // which only happens when the keys are loaded
            walletReady() {
                wallet = pending; // when using svelte bind:wallet
                dispatch('walletReady', { wallet }); // when using vanilla JS
                // overwrite any other arweave wallets on the window object
                // @ts-ignore
                window.arweaveWallet = wallet.arweaveWalletAPI;
                if (wallet) {
                    // hack: await 250 milliseconds
                    setTimeout(() => {
                        hide();
                    }, 250);
                }
                return true;
            }
        }
    });
    pending = await connection.promise;
    show();
}
const connect = () => {
    if (src === inputUrl)
        return;
    src = '';
    src = inputUrl;
    data.loading = true;
    dispatch('inputUrl', inputUrl); // when using vanilla JS
};
const disconnect = () => wallet.disconnect();
const togglePopup = () => window.open(inputUrl);
function handleKeydown(event) {
    if (event.key === 'Enter' && focused)
        connect();
}
$: iframe && iframe.addEventListener('load', handleIframeLoad);
$: popupIcon = wallet?.keepPopup ? 'close' : 'launch';
$: connectionIcon = wallet?.address ? 'unplug' : 'plug';
$: iframeOffsetWidth && wallet && wallet?.setWidth(iframeOffsetWidth);
</script>

<svelte:window on:keydown={handleKeydown} />

<div class="flex flex-col m-2 max-w-full h-screen">
	<div
		class="flex flex-row space-between items-center"
		bind:offsetHeight={topOffsetHeight}
		bind:offsetWidth={topOffsetWidth}
		style="--topOffsetHeight: {topOffsetHeight};"
	>
		<a class="flex-0 hidden md:flex" href="https://PeerPiper.io" target="_blank" rel="noreferrer">
			<div class="actions logo">
				<Logo />
			</div></a
		>
		<div class="flex-shrink flex flex-col w-full pl-2 md:p-2">
			<input
				class="border-b-4 border-toxic url pl-0 p-2 pr-0 m-2 text-white bg-none border-none text-sm sm:text-base outline-none"
				{placeholder}
				on:focus={() => (focused = true)}
				on:blur={() => (focused = false)}
				bind:value={inputUrl}
			/>
			<span class="border-b-4 border-toxic flex-1 relative -top-2" />
		</div>
		<div class="hidden md:flex">
			<div
				class={data?.loading
					? 'action dim'
					: 'action' && wallet?.address
					? ' connected '
					: ' disconnected '}
			>
				<IconButton
					icon={connectionIcon}
					on:keypress={() => {
						wallet?.address ? disconnect() : connect();
					}}
					on:click={() => {
						wallet?.address ? disconnect() : connect();
					}}
					><span class="{wallet?.address ? ' connected ' : ' disconnected '} hidden sm:flex">
						{data.loading || !src ? 'Loading...' : 'Load'}
					</span></IconButton
				>
			</div>
		</div>
	</div>
	<div
		class="iframe flex"
		style="height: calc({iframeParentHeight}px + 18px)"
		bind:offsetWidth={iframeOffsetWidth}
	>
		<iframe
			class="w-full border-none min-h-full"
			title="Web Wallet"
			bind:this={iframe}
			{src}
			allow="clipboard-read 'self' 'src'; clipboard-write 'self' 'src';"
		/>
		<!-- allow="clipboard-read 'self' 'src' {src}; clipboard-write 'self' 'src' {src};" -->
	</div>
</div>

<style>.relative {
		position: relative;
}.-top-2 {
		top: -0.5rem;
}.m-2 {
		margin: 0.5rem;
}.flex {
		display: flex;
}.hidden {
		display: none;
}.h-screen {
		height: 100vh;
}.min-h-full {
		min-height: 100%;
}.w-full {
		width: 100%;
}.max-w-full {
		max-width: 100%;
}.flex-1 {
		flex: 1 1 0%;
}.flex-shrink {
		flex-shrink: 1;
}.flex-row {
		flex-direction: row;
}.flex-col {
		flex-direction: column;
}.items-center {
		align-items: center;
}.border-b-4 {
		border-bottom-width: 4px;
}.border-none {
		border-style: none;
}.border-toxic {
		--tw-border-opacity: 1;
		border-color: rgb(14 255 2 / var(--tw-border-opacity));
}.bg-none {
		background-image: none;
}.p-2 {
		padding: 0.5rem;
}.pl-2 {
		padding-left: 0.5rem;
}.pl-0 {
		padding-left: 0px;
}.pr-0 {
		padding-right: 0px;
}.text-sm {
		font-size: 0.875rem;
		line-height: 1.25rem;
}.text-base {
		font-size: 1rem;
		line-height: 1.5rem;
}.text-white {
		--tw-text-opacity: 1;
		color: rgb(255 255 255 / var(--tw-text-opacity));
}.outline-none {
		outline: 2px solid transparent;
		outline-offset: 2px;
}.iframe {
		min-height: 500px;
	}.logo {
		flex: 0 0 auto;
		position: relative;
		opacity: 1;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: calc(var(--spacing) / 2);
	}.url {
		flex: 1 1 0;
		background-color: var(--background);
	}.actions {
		display: flex;
	}.actions:last-child {
		padding-right: calc(var(--spacing) / 2);
	}.connected {
		color: greenyellow;
		text-shadow: 1px 1px 3px black;
	}.disconnected {
		color: #e0f7fa;
		text-shadow: 1px 1px 3px black;
	}@media(min-width:640px){.sm\:flex{display:flex;}.sm\:text-base{font-size:1rem;line-height:1.5rem;}}@media(min-width:768px){.md\:flex{display:flex;}.md\:p-2{padding:0.5rem;}}</style>
